import './import-jquery'
import 'gsap'
const AOS = require('aos')
import 'velocityjs'
import 'blast-text'
import anime from 'animejs'
import Highway from '@dogstudio/highway'
import Fade from './transition'
import skrollr from 'skrollr'
require('waypoints/lib/jquery.waypoints')

const H = new Highway.Core({
    transitions: {
        default: Fade
    }
})


$(document).ready(function () {

      // skrollr script

      var s = skrollr.init();
          skrollr.init({
              forceHeight: !1,
              easing: "outCubic",
              mobileCheck: function () {
                  return (/Android|iPhone|iPad|iPod|BlackBerry/i).test(navigator.userAgent || navigator.vendor || window.opera);
              }
          });
  
      if ($(window).width() < 768) {
          s = skrollr.init().destroy();
      }

    $(window).on('load', function () {
        $(".preloader").fadeOut();
    });

    $('.hamburger').click(function () {
        $('.main-menu').addClass('menu-show');
    });
    $('.back-btn').click(function () {
        $('.main-menu').removeClass('menu-show');
    });

    $(".back-btn a").click(function (e) {
        e.preventDefault();
    })

    TweenMax.staggerFrom('.main-menu li', 1, {
        y: -50,
        opacity: 0,
        delay: .2,
        ease: Expo.easeOut
    }, 0.2);
    TweenMax.fromTo('.profile__img', 3, {
        y: 60,
        opacity: 0,
        ease: Expo.easeOut,
        delay: .5,
    }, {
        y: 0,
        opacity: 1,
        ease: Expo.easeOut
    });
    AOS.init({
        disable: function () {
            var maxWidth = 800;
            return window.innerWidth < maxWidth;
        },
        once: true,
    });


});

$(".hello h1").blast({
    delimiter: "character",
});
$(".main-menu li a").blast({
    delimiter: "character",
});
let a = 0;
let b = 0;
$(".hello .blast").each(function () {
    if (a == 300) {
        a = 400;
    }
    if (a == 1100) {
        a = 1200;
    }
    var el = $(this);
    setTimeout(function () {
        el.addClass('animated bounceIn');
    }, a);

    if (a < 1200) {
        a = a + 100;
    } else {
        a = a + 80;
    }
});
setTimeout(function () {

    $(".hello .blast").removeClass('animated bounceIn');
    $(".hello .blast").css('opacity', 1);
}, 3000);
document.addEventListener('DOMContentLoaded', () => {
    let navLinks = document.querySelectorAll('.link');
    navLinks.forEach((navLink) => {
        navLink.addEventListener('mouseenter', () => {
            let letter = navLink.querySelectorAll('.link .blast');
            anime.remove(letter);
            anime.timeline({
                    targets: letter,
                })
                .add({
                    translateX: [0, -90],
                    opacity: [1, 0],
                    easing: 'easeInExpo',
                    duration: 600,
                    delay: (el, i) => 30 * i,
                })
                .add({
                    translateX: [60, 0],
                    opacity: [0, 1],
                    easing: 'easeOutExpo',
                    duration: 1100,
                    delay: (el, i) => 30 * i,
                })
        })
    })
});
$(".magicwall-grid li").click(function () {
    window.open($(this).data("href"), '_blank')
})


new Waypoint({
    element: $('.animaper'),
    handler: function() {
        $('.animaper').find(".skillbar-bg").each(function () {
            $(this).find(".custom-skillbar").delay(600).animate({
                width: $(this).attr("data-percent")
            }, 800)
            var percent = $(this).data("percent");
            var width = $(".skill-bar-percent").width() / 5.9;
            var actual = (parseInt(percent) - width) + "%";
            $(this).find(".skill-bar-percent").css("left", actual);
            $(".skill-bar-percent").addClass("animatein");
        })
    },
    offset: 380
  })