import Highway from '@dogstudio/highway'
import { TimelineLite } from 'gsap';

class Fade extends Highway.Transition{
    in({from, to ,done}){
        const tl = new TimelineLite()
        tl.fromTo(to,1,{left:"-100%",ease: Expo.easeOut},{left:"0%" ,onComplete:function(){
            from.remove()
            done()
        }}).delay(.4)
    }
    out({from, done}){
        const tm = new TimelineLite();
        tm.fromTo(from.children[0],.9,{ width:"0%" },{ width:"200%" })
        .fromTo(from.children[0],1,{x:0},{x:"100%"})
        done()
    }
}

export default Fade